@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
    font-family: "Righteous", cursive;
}

.App {
    font-family: "Inconsolata", monospace;
    font-size: 20px;
    overflow-y: hidden; /* Hide vertical scrollbar */
    overflow-x: hidden; /* Hide horizontal scrollbar */
}

:root {
    --nav-color: hsl(210, 11%, 15%);
    --nav-color-hover: hsl(210, 11%, 45%);
    --nav-border-color: hsl(210, 11%, 45%);
    --light-color: hsl(209deg 62% 30%);
    --font-color: hsl(0, 0%, 100%);
    --font-color-hover: hsl(0, 0%, 40%);
}

.navigation_container{
    background-color: var(--nav-color);
    color: white;
}

.navbar-light .navbar-nav .nav-link{
    color: var(--font-color)!important;
}

.navbar-light .navbar-brand{
    color: var(--font-color)!important;
}

.navbar-light .navbar-toggler-icon{
    -webkit-filter: invert(1);
            filter: invert(1);
}

.links a:hover{
    background-color: var(--font-color-hover);
}
.main-info{
    display: flex;
    position: absolute;
    color: var(--font-color);
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center ;
    z-index: 1;
    font-size: 22px;
}
.about_container {
    margin: 3rem 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
   
}

#about-me.about_container{
     background-color: hsl(210deg 27% 24%);
     margin: 0;
     padding: 100px;
}

.about_container img {
    border-radius: 50%;
    width: 200px;
    border: 0.5px solid rgb(218, 218, 218);
    margin-bottom: 1rem;
}

.about_text {
    max-width: 600px;
    color: 0.5px solid rgb(218, 218, 218);
}

/* ----------------MEDIA QUERIES---------------- */

@media only screen and (max-width: 900px) {
    .about_container {
        flex-direction: column;
    }
}

@media only screen and (max-width: 700px) {
    .about_text {
        max-width: 300px;
    }
}


.technologies_container {
    background-color: var(--nav-color);
    color: var(--nav-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem;
}

.tech_cards_container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1100px;
    margin: 1rem 0;
}

.tech_card {
    width: 15rem;
    height: 10rem;
    background-color: var(--font-color);
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    border-bottom: 5px solid var(--nav-color);
    -webkit-transform: all 0.1;
            transform: all 0.1;
}

.tech_icons {
    height: 70px;
    background-color: #6c757d;
    border-radius: 50%;
}

.technologies_text {
    text-align: "center";
    color: rgb(218, 218, 218);
    font-weight: bold;
    font-size: -webkit-xxx-large;
}

/* ----------------MEDIA QUERIES---------------- */

@media only screen and (max-width: 700px) {
    .tech_card {
        width: 8rem;
        height: 8rem;
    }

    .technologies_container{
        padding: 0%;
    }



}

#portfolio.portfolio_main-container,
#portfolio_card_container {
    background-color: hsl(210deg 27% 24%);
}

#portfolio.portfolio_main-container h1,
#portfolio.portfolio_main-container p {
    text-align: center;
    padding-top: 3rem;
    color: 0.5px solid rgb(218, 218, 218);
    font-weight: bold;
    font-size: -webkit-xxx-large;
}

#portfolio_card_container {
    width: 50%;
    padding: 10px;
    border: none;
}

.portfolio_image:hover {
    cursor: pointer;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
}

.portfolio_click_info {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: var(--font-color);
    display: none;
}

.modal-content {
    font-family: "Inconsolata", monospace;
    font-weight: bold;
}

.portfolio_image:hover + .portfolio_click_info {
    display: block;
}

.modal-backdrop-show {
    opacity: 0.1 !important;
}

#portfolio_modal_link {
    padding: 0 0 1rem 1rem;
}

#portfolio_modal_link:link {
    text-decoration: none;
}

.portfolio_text {
    color: rgb(218, 218, 218);
}

@media only screen and (max-width: 600px) {
    #portfolio_card_container {
        padding: 0.5rem 2.5rem;
        border: none;
        width: 100%;
        object-fit: cover;
    }

    .portfolio_image{
        object-fit: cover;
        height: 154px;
    }
}

.contactMe_container{
    background-color: var(--nav-color);
    color: aliceblue;
    padding: 1rem;
    padding-top: 30px;
}

.contactMe_data_links{
    display: flex;
    justify-content: space-evenly;
}

.contactMe_data_links a{
    color: white;
    text-decoration: none;
    text-transform: uppercase;
}

.contactMe_data_links a:hover{
    color: var(--light-color);
}

@media only screen and (max-width: 600px) {
    .contactMe_data_links{
        flex-direction: column;
    }  
}
